import React from 'react';
import './App.scss';
import { RiftGate, RiftProvider } from 'rift-router';
import { flattenMessages } from './modules/commons/utils/utils';
import { addLocaleData, IntlProvider } from 'react-intl';
import es from 'react-intl/locale-data/es';
import en from 'react-intl/locale-data/en';
import fr from 'react-intl/locale-data/fr';
/* Internationalization */
import enMessage from './translations/en.json';
import esMessage from './translations/es.json';
import { observer, useComputed } from 'mobx-react-lite';
import { routeStore } from './core/stores/RouterStore';
import store from './core/stores/RootStore';
import { AppStore, ITheme, themes } from './core/stores/AppStore';

export interface AppContextInterface {
  app: AppStore;
}

export const AppThemeContext = React.createContext<ITheme>(themes.light);
export const StoreAppContext = React.createContext<AppContextInterface>(store);

const App = observer(() => {
  const messages = useComputed(() => {
    const lang = store.app!.lang;
    switch (lang.languageCode) {
      case 'es':
        addLocaleData(es);
        return esMessage;
      case 'en':
        addLocaleData(en);
        return enMessage;
      case 'fr':
        addLocaleData(fr);
        return enMessage;

      default:
        addLocaleData(es);
        return esMessage;
    }
  });

  return (
    <StoreAppContext.Provider value={store}>
      <AppThemeContext.Provider value={store.app.theme}>
        <IntlProvider
          locale={store.app.lang.languageCode}
          key={store.app.lang.languageId}
          messages={flattenMessages(messages)}
        >
          <div id={'App'} className="App">
            <RiftProvider routes={routeStore}>
              <RiftGate />
            </RiftProvider>
          </div>
        </IntlProvider>
      </AppThemeContext.Provider>
    </StoreAppContext.Provider>
  );
});

export default App;
