import React, { useContext, useEffect } from 'react';
import { Col, Grid, Row } from 'react-flexbox-grid';
import { Observer } from 'mobx-react-lite';
import { useRouter } from 'rift-router';

import RecentAlert from './RecentAlert';
import HomeTaskCard from './task_card/HomeTaskCard';
import { HomeLayoutContainer } from './styles';
import { AppThemeContext, StoreAppContext } from '../../../App';
import { HomeStoreContext } from '../container/HomeContainer';
import { IframeSD } from '../../commons/iframe/IframeSD';
import { VCardPanel, VSpinner } from 'gsi-vx-ui';
import { InjectedIntlProps, injectIntl } from 'react-intl';

const HomeLayout: React.FunctionComponent<InjectedIntlProps> = (props: InjectedIntlProps) => {
  const AppContext = useContext(StoreAppContext);
  const ThemeContext = useContext(AppThemeContext);
  const HomeContext = useContext(HomeStoreContext);
  const router = useRouter();
  useEffect(() => HomeContext.getHomeGraph(), []);

  return (
    <Observer
      render={() => (
        <HomeLayoutContainer>
          <Grid fluid>
            <Row center={'xs'}>
              <Col xs={12}>
                <VCardPanel
                  headerText={props.intl.formatMessage({ id: 'home.dashboard.analyticsGraph' })}
                  headerBackgroundColor={ThemeContext.headers}
                  headerColor={'#FFFFFF'}
                >
                  {HomeContext.graphUrl ? <IframeSD url={HomeContext.graphUrl} /> : <VSpinner />}
                </VCardPanel>
              </Col>
            </Row>
            <Row>
              <Col xs={4}>
                <br />
                <RecentAlert
                  router={router}
                  alerts={AppContext.app!.recentAlerts}
                  load={AppContext.app!.recentAlerts.length! > 0}
                />
              </Col>
              {AppContext.app!.operations.length > 0 && (
                <Col xs={12} lg={8}>
                  <HomeTaskCard operations={AppContext.app!.operations} load={AppContext.app!.isLoading} />
                </Col>
              )}
            </Row>
          </Grid>
        </HomeLayoutContainer>
      )}
    />
  );
};

export default injectIntl(HomeLayout);
