import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { VSuspenseLoading, VTabsPanel } from 'gsi-vx-ui';
import { ITabsPanelTypes } from 'gsi-vx-ui/lib/types/TabsPanel/';
import { OpenOrdersStore } from '../../stores/OpenOrdersStore';
import { IncompleteStore } from '../../stores/IncompleteStore';
import { observable } from 'mobx';
import { InjectedIntlProps, injectIntl } from 'react-intl';

const OrdersComponent = React.lazy(() => import('../open_orders/OrdersComponent'));
const DeliveryExceptionsComponent = React.lazy(() => import('../delivery_exceptions/DeliveryExceptionsComponent'));

interface IPropsOrderManagementLayout extends InjectedIntlProps {
  open_orders_store?: OpenOrdersStore;
  incomplete_orders_store?: IncompleteStore;
  totalExcept: number;
  changeColor(module: number, color: string): void;
  setActiveList(list: number): void;
}

interface IStateOrderManagementLayout {
  activeTab: string;
}
@inject('open_orders_store', 'incomplete_orders_store')
@observer
class OrderManagementLayout extends Component<IPropsOrderManagementLayout, IStateOrderManagementLayout> {
  @observable
  activeTab: string;
  constructor(props: IPropsOrderManagementLayout) {
    super(props);
    this.activeTab = '1';

    // todo fix router inject (yoendy)
    // if (this.props.router!.search.hasOwnProperty('child')) {
    //   this.activeTab = this.props.router!.search.child;
    // }
  }

  handlerChangeTab = (data: ITabsPanelTypes) => {
    this.setState({ ...this.state, activeTab: data.key });
    data.key === '1' ? this.props.setActiveList(11) : this.props.setActiveList(10);
  };

  render() {
    const { count } = this.props.open_orders_store!;
    const modules: ITabsPanelTypes[] = [
      {
        icon: { intent: 'primary', icon: 'folder-close', iconSize: 16 },
        label: `${this.props.intl.formatMessage({ id: 'delivery.order.open.title_list_open' })} (${count})`,
        key: '1',
        content: <OrdersComponent />
      },
      {
        key: '2',
        icon: { intent: 'primary', icon: 'folder-close', iconSize: 16 },
        label: `${this.props.intl.formatMessage({ id: 'delivery.order.review.textDelExcep' })}`,
        content: <DeliveryExceptionsComponent />,
        dataBadge: this.props.totalExcept.toString()
      }
    ];

    return (
      <div style={{ marginBottom: 5, marginTop: 5 }}>
        <VSuspenseLoading>
          <VTabsPanel active={this.activeTab} padding={'0px'} tabList={modules} handleChange={this.handlerChangeTab} />
        </VSuspenseLoading>
      </div>
    );
  }
}

export default injectIntl(OrderManagementLayout);
