import React, { useContext } from 'react';
import { observer } from 'mobx-react-lite';
import { RiftGate, useRouter } from 'rift-router';

import { Container, CSidebarPushable, CSidebarPusher } from './styles';

import MainNavbar from './MainNavbar';
import MainSidebar from './MainSidebar';
import DetailsDrawer from './Drawers/DetailsDrawer';
import IRecentAlert from '../../types/RecentAlert';
import { AppThemeContext, StoreAppContext } from '../../../App';

const Main = observer(() => {
  const AppStoreContext = useContext(StoreAppContext);
  const ThemeContext = useContext(AppThemeContext);

  const router = useRouter();

  const getRouteParams = (object: any) => {
    let params = '';
    const keys = Object.keys(object);
    if (keys.length > 0) {
      keys.map((key, index) => {
        const modifier = index === 0 ? '?' : '&&';
        params += `${modifier}${key}=${object[key]}`;
      });
    }
    return params;
  };

  const navigateAlert = (alert: IRecentAlert) => {
    router.to(`/delivery${getRouteParams(alert.route)}`);
    AppStoreContext.app!.setIsOpenProcess(false);
  };

  return (
    <CSidebarPushable>
      <MainSidebar />
      <CSidebarPusher>
        <MainNavbar />
        <Container background={ThemeContext.accent_gray}>
          <RiftGate />
        </Container>
      </CSidebarPusher>
      <DetailsDrawer
        process={AppStoreContext.app!.operations}
        alerts={AppStoreContext.app!.recentAlerts}
        isOpen={AppStoreContext.app!.isOpenProcess}
        onClose={() => AppStoreContext.app!.setIsOpenProcess(false)}
        onNavigate={navigateAlert}
      />
    </CSidebarPushable>
  );
});

export default Main;
