import BasePaginateService from '../../../../../core/services/BasePaginateService';
import { AxiosPromise } from 'axios';
import qs from 'qs';

export class DeliveryExceptionServices extends BasePaginateService {
  constructor() {
    super('/');
  }

  public getStatus(params: any): AxiosPromise<any[]> {
    return this.httpAxios
      .get('listDeliveryStatus', { params })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
  public exportExcel(url: string, params: any, size: number): AxiosPromise<any> {
    return this.httpAxios
      .get(url, {
        params: { size, data: JSON.stringify(params) },
        paramsSerializer: params => {
          return qs.stringify(params, { arrayFormat: 'brackets' });
        },
        headers: {
          'Content-Type': 'application/vnd.ms-excel;charset=utf-8'
        },
        maxContentLength: 10000,
        responseType: 'arraybuffer',
        timeout: 10000000
      })
      .then(response => response)
      .catch(err => {
        throw err;
      }) as AxiosPromise<any>;
  }
}

const deliveryExceptionServices = new DeliveryExceptionServices();
export default deliveryExceptionServices;
