import React, { useContext } from 'react';
import { observer } from 'mobx-react-lite';
import { useRouter } from 'rift-router';

import { CSidebar, IconSidebar, LabelSidebar, MenuItem, VisibleLabel } from './styles';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { ModulesKey } from '../../types/ModulesNavigateType';
import { AppThemeContext, StoreAppContext } from '../../../App';
import { ITheme } from '../../stores/AppStore';

const MainSidebar = observer((props: InjectedIntlProps) => {
  const AppStoreContext = useContext(StoreAppContext);
  const router = useRouter();

  const linkTo = (path: string, activeModule: ModulesKey) => {
    AppStoreContext.app!.setModulesNavigate({ activeModule });
    router.to(path);
  };

  return (
    <AppThemeContext.Consumer>
      {(themeContext: ITheme) =>
        themeContext && (
          <CSidebar
            background={themeContext.sidebar}
            textcolor={themeContext.sidebar_text}
            collapse={AppStoreContext.app!.isCollapseMenu}
          >
            <MenuItem
              active={AppStoreContext.app!.modulesNavigate.activeModule === 'home'}
              onClick={(e: any) => linkTo('/home', 'home')}
            >
              <IconSidebar textcolor={themeContext.sidebar_text} icon="home" color="white" iconSize={24} />
              <VisibleLabel>
                <LabelSidebar textcolor={themeContext.sidebar_text}>{`${props.intl.formatMessage({
                  id: 'layout.sidebar.home'
                })}`}</LabelSidebar>
              </VisibleLabel>
            </MenuItem>
            <MenuItem
              active={AppStoreContext.app!.modulesNavigate.activeModule === 'deliveryManagement'}
              onClick={(e: any) => linkTo('/delivery', 'deliveryManagement')}
            >
              <IconSidebar textcolor={themeContext.sidebar_text} icon="drive-time" color="white" iconSize={24} />
              <VisibleLabel>
                <LabelSidebar textcolor={themeContext.sidebar_text}>{`${props.intl.formatMessage({
                  id: 'layout.sidebar.delivery_manager'
                })}`}</LabelSidebar>
              </VisibleLabel>
            </MenuItem>
            <MenuItem
              active={AppStoreContext.app!.modulesNavigate.activeModule === 'config'}
              onClick={(e: any) => linkTo('/setup', 'config')}
            >
              <IconSidebar textcolor={themeContext.sidebar_text} icon="cog" color="white" iconSize={24} />
              <VisibleLabel>
                <LabelSidebar textcolor={themeContext.sidebar_text}>{`${props.intl.formatMessage({
                  id: 'layout.sidebar.setup'
                })}`}</LabelSidebar>
              </VisibleLabel>
            </MenuItem>
            <MenuItem
              active={AppStoreContext.app!.modulesNavigate.activeModule === 'adminAccount'}
              onClick={(e: any) => linkTo('/account', 'adminAccount')}
            >
              <IconSidebar textcolor={themeContext.sidebar_text} icon="user" color="white" iconSize={24} />
              <VisibleLabel>
                <LabelSidebar textcolor={themeContext.sidebar_text}>{`${props.intl.formatMessage({
                  id: 'layout.sidebar.account_manager'
                })}`}</LabelSidebar>
              </VisibleLabel>
            </MenuItem>
            <MenuItem
              active={AppStoreContext.app!.modulesNavigate.activeModule === 'dashboard'}
              onClick={(e: any) => linkTo('/dashboard', 'dashboard')}
            >
              <IconSidebar textcolor={themeContext.sidebar_text} icon="dashboard" color="white" iconSize={24} />
              <VisibleLabel>
                <LabelSidebar textcolor={themeContext.sidebar_text}>{`${props.intl.formatMessage({
                  id: 'layout.sidebar.performance_dashboard'
                })}`}</LabelSidebar>
              </VisibleLabel>
            </MenuItem>
            <MenuItem
              active={AppStoreContext.app!.modulesNavigate.activeModule === 'dashboard'}
              onClick={(e: any) => linkTo('/dashboard', 'dashboard')}
            >
              <IconSidebar textcolor={themeContext.sidebar_text} icon="help" color="white" iconSize={24} />
              <VisibleLabel>
                <LabelSidebar textcolor={themeContext.sidebar_text}>{`${props.intl.formatMessage({
                  id: 'layout.sidebar.support'
                })}`}</LabelSidebar>
              </VisibleLabel>
            </MenuItem>
          </CSidebar>
        )
      }
    </AppThemeContext.Consumer>
  );
});

export default injectIntl(MainSidebar);
