import React, { useContext, useState } from 'react';
import { observer } from 'mobx-react-lite';

import Login from '../../components/login/login';
import { required } from '../../../../commons/form/Validators';
import { FieldState, FormState } from 'formstate';
import { UserCredentials } from '../../../../../core/models/UserCredentials';
import authService from '../../../../../core/services/AuthServices';
import { StoreAppContext } from '../../../../../App';
import { useRouter } from 'rift-router';
import { userStore, UserStore } from '../../UserStore';

export const LoginContainer = observer(() => {
  const AppStoreContext = useContext(StoreAppContext);
  const router = useRouter();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(false);
  const userStor: UserStore = userStore;

  const form = new FormState<any>({
    username: new FieldState('').validators(required),
    password: new FieldState('').validators(required)
  });

  const login = () => {
    form.validate().then(res => {
      setError(res.hasError);
      if (!res.hasError) {
        const user = new UserCredentials(form.$.username.value, authService.encriptPassword(form.$.password.value));

        const userForm = new FormData();
        userForm.append('username', user.username);
        userForm.append('password', user.password);
        userForm.append('grant_type', user.grant_type);
        setIsLoading(true);
        authService
          .login(userForm)
          .then(response => {
            AppStoreContext.app!.storeUserCredentials(response.data);
          })
          .then(() => {
            userStor
              .getMe()
              .then(response => {
                AppStoreContext.app!.setUser(response);
                AppStoreContext.app!.setLang(response.language);

                router!.to('/');
                AppStoreContext.app!.setModulesNavigate({ activeModule: 'home' });
                setIsLoading(false);
              })
              .catch((error: any) => {
                console.log(error);
                setError(true);
                setIsLoading(false);
              });
          })
          .catch((error: any) => {
            console.log(error);
            setError(true);
            setIsLoading(false);
          });
      }
    });
  };
  return <Login onLogin={login} form={form} error={error} isLoading={isLoading} />;
});
