import * as React from 'react';
import { Icon, IconName, MaybeElement } from '@blueprintjs/core';

export const RenderEmptyData = (props: ISettingEmptyData) => {
  const text: string = props.text || 'No data';
  const color: string = props.color || 'rgba(49, 59, 67, 0.27)';
  const height: string = props.height || '20vh';
  const backgroundColor: string = props.backgroundColor || 'rgb(245, 245, 245)';

  const iconSize: number = props.iconSize || 28;

  const textSize: number = props.textSize || 22;

  const icon: IconName | MaybeElement = props.icon || 'media';

  const customerIcon = props.customerIcon;

  const renderIcon = customerIcon ? customerIcon : <Icon icon={icon} iconSize={iconSize} />;

  return (
    <div
      style={{
        height,
        backgroundColor,
        color,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
      }}
    >
      <div style={{ textAlign: 'center' }}>
        {renderIcon}
        <p style={{ color, fontSize: textSize, fontWeight: 400 }} className="bp3-heading">
          {text}
        </p>
      </div>
    </div>
  );
};

export interface ISettingEmptyData {
  textSize?: number;
  iconSize?: number;
  color?: string;
  backgroundColor?: string;
  height?: string;
  text?: string;
  icon?: IconName | MaybeElement;
  customerIcon?: any;
}
