import React, { createContext } from 'react';
import DeliveryLayout from '../components/DeliveryLayout';
import deliveryStore, { DeliveryStore } from '../DeliveryStore';
import { observer } from 'mobx-react-lite';

export const StoreDeliveryContext = createContext<DeliveryStore>(deliveryStore);

const DeliveryContainer = observer(() => {
  return (
    <StoreDeliveryContext.Provider value={deliveryStore}>
      <DeliveryLayout />
    </StoreDeliveryContext.Provider>
  );
});

export default DeliveryContainer;
