import React, { ReactNode, useContext, useEffect } from 'react';
import { VCardPanel, VSpinner, VTable } from 'gsi-vx-ui';
import { ISortResult, IVTableProps } from 'gsi-vx-ui/lib/types/Table/Table';
import { Col, Grid, Row } from 'react-flexbox-grid';
import { SummaryShift } from '../../models/summaryShift';
import moment from 'moment';
import { Icon } from '@blueprintjs/core';
import { IVWidgetTableProps } from 'gsi-vx-ui/lib/types/Table/Widget/Widget';
import { statusOptions, truckUrgencyColor } from '../../enums/enums';
import { AppThemeContext } from '../../../../../../App';
import { FilterOperator } from '../../../../../../core/types/FilterType';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import PageComponent from '../../../../../commons/components/Paginator/PagePaginatorComponent';
import { RouteStoreContext, SummaryStoreContext } from '../../containers/SummaryContainer/SummaryContainer';
import { Observer } from 'mobx-react-lite';

interface IDeliverySummaryRouteProps extends InjectedIntlProps {
  onSelectShift: (element: any) => void;
}

const iconStyle = {
  backgroundColor: '#c23030',
  color: 'white',
  borderRadius: '50px'
};

const DeliverySummaryRoute: React.FunctionComponent<IDeliverySummaryRouteProps> = (
  props: IDeliverySummaryRouteProps
): JSX.Element => {
  const operator: FilterOperator = 'EQUALITY';
  const StoreContext = useContext(RouteStoreContext);
  const SummaryContext = useContext(SummaryStoreContext);
  const ThemeContext = useContext(AppThemeContext);
  StoreContext.route!.addFilter({
    operator,
    field: 'date',
    value: moment(SummaryContext.store!.date).format('YYYY-MM-DD')
  });
  if (StoreContext.route!.elements.length === 0) {
    StoreContext.route!.getElements();
  } else {
    props.onSelectShift({ element: StoreContext.route!.elements[0], tab: 'summary' });
  }
  const onSelect = (data: any) => {
    const element = StoreContext.route!.elements.find((element: any) => element.shiftId === data.shiftId) || data;
    StoreContext.route!.setElement(element);
  };

  const onSort = (sort: ISortResult) => {
    if (sort.order === 'NONE') {
      StoreContext.route!.removeOrder({
        property: StoreContext.route!.columns[sort.columnIndex],
        direction: sort.order
      });
    } else {
      StoreContext.route!.addOrder({
        property: StoreContext.route!.columns[sort.columnIndex],
        direction: sort.order
      });
    }
    StoreContext.route!.getElements();
  };
  const onPageChange = (data: any) => {
    if (data.paginationData.currentPage !== StoreContext.route!.page || data.pageLimit !== StoreContext.route!.size) {
      StoreContext.route!.setSize(data.pageLimit);
      data.paginationData.currentPage > 0 && StoreContext.route!.setPage(data.paginationData.currentPage);
    }
  };
  const onOrdering = (columns: string[]) => {
    StoreContext.route!.setColumns(columns);
  };
  const transformRoutesList = (): IVTableProps => {
    const customWidget: IVWidgetTableProps = {
      column: 'status',
      widget: {
        type: 'CUSTOMERCOMPONENT',
        cusmtomerCell: {
          renderCustomer: (value: any): ReactNode => {
            let icon = <Icon icon={'tick-circle'} iconSize={15} intent={'warning'} />;
            switch (value.status) {
              case statusOptions.SUCCESS:
                icon = <Icon icon={'tick-circle'} iconSize={15} intent={'success'} />;
                break;
              case statusOptions.MISSING:
                icon = <Icon icon={'warning-sign'} iconSize={15} intent={'warning'} />;
                break;
              case statusOptions.INCOMPLETE:
                icon = <Icon style={iconStyle} icon={'cross'} iconSize={15} intent={'danger'} />;
                break;
              case statusOptions.TRUCK:
                icon = <Icon icon={'drive-time'} iconSize={15} color={truckUrgencyColor[value.truckColor]} />;
                break;
            }
            return <div>{icon}</div>;
          }
        }
      }
    };
    const widgetsCell: IVWidgetTableProps[] = [customWidget];
    const data = StoreContext.route!.elements.map((item: SummaryShift) => {
      return {
        status: { status: item.status, truckColor: item.urgency },
        urgency: item.urgency,
        truckId: item.truckId,
        truckName: item.truckName,
        driverFullName: item.driverFullName,
        driverId: item.driverId,
        shiftId: item.shiftId,
        startTime: item.startTime,
        stopsPlanned: item.stopsPlanned,
        stopsAdded: item.stopsAdded,
        totalStops: item.totalStops,
        stopsCompleted: item.stopsCompleted,
        nda: item.nda,
        ndu: item.ndu,
        totalStopsCompleted: item.totalStopsCompleted,
        percentCompleted: `${item.percentCompleted}%`,
        qtyDelivered: item.qtyDelivered,
        totalMiles: item.totalMiles,
        endTime: item.endTime
      };
    });
    return {
      data,
      widgetsCell,
      striped: true,
      cellSelectionType: 'ENTIRE_ROW',
      actionsSelection: { onSelectionChange: onSelect },
      columns: StoreContext.route!.columns,
      columns_name: {
        status: props.intl.formatMessage({ id: 'delivery.summary.deliveries.status' }),
        truckName: props.intl.formatMessage({ id: 'delivery.order.open.truck' }),
        driverFullName: props.intl.formatMessage({ id: 'delivery.order.open.driver' }),
        shiftId: props.intl.formatMessage({ id: 'delivery.summary.deliveries.shiftID' }),
        startTime: props.intl.formatMessage({ id: 'delivery.summary.deliveries.startShift' }),
        stopsPlanned: props.intl.formatMessage({ id: 'delivery.summary.deliveries.stopsPlanned' }),
        stopsAdded: props.intl.formatMessage({ id: 'delivery.summary.deliveries.stopsAdd' }),
        totalStops: props.intl.formatMessage({ id: 'delivery.summary.deliveries.totalStops' }),
        stopsCompleted: props.intl.formatMessage({ id: 'delivery.summary.deliveries.stopsCmp' }),
        nda: props.intl.formatMessage({ id: 'delivery.summary.deliveries.nda' }),
        ndu: props.intl.formatMessage({ id: 'delivery.summary.deliveries.ndu' }),
        totalStopsCompleted: props.intl.formatMessage({ id: 'delivery.summary.deliveries.totalSC' }),
        percentCompleted: props.intl.formatMessage({ id: 'delivery.summary.deliveries.percentCompl' }),
        qtyDelivered: props.intl.formatMessage({ id: 'delivery.summary.deliveries.qtyDel' }),
        totalMiles: props.intl.formatMessage({ id: 'delivery.summary.deliveries.totalMil' }),
        endTime: props.intl.formatMessage({ id: 'delivery.summary.deliveries.endShift' })
      },
      footer: (
        <PageComponent
          currentPage={StoreContext.route!.page}
          onPageChanged={onPageChange}
          pageLimit={StoreContext.route!.size}
          totalRecords={StoreContext.route!.count}
        />
      ),
      reordering: true,
      onOrderColumns: onOrdering,
      typeHeightRow: 'HALF',
      sortable: {
        onSort,
        setupsOrden: StoreContext.route!.sortOrderForTable,
        columns: [
          'status',
          'truckName',
          'driverFullName',
          'shiftId',
          'startTime',
          'stopsPlanned',
          'stopsAdded',
          'totalStops',
          'stopsCompleted',
          'nda',
          'ndu',
          'totalStopsCompleted',
          'percentCompleted',
          'qtyDelivered',
          'totalMiles',
          'endTime'
        ]
      }
    };
  };
  return (
    <Observer
      render={() => {
        useEffect(() => {
          props.onSelectShift({ element: StoreContext.route!.element, tab: 'summary' });
        }, [StoreContext.route!.element]);
        const data = transformRoutesList();
        return (
          <Grid fluid>
            <Row>
              <Col xs>
                <VCardPanel
                  headerTextJustify={'center'}
                  headerText={`Delivery Shift (${StoreContext.route!.count})`}
                  bodyPadding={'0'}
                  headerBackgroundColor={ThemeContext.headers}
                  headerColor={'#FFFFFF'}
                >
                  {StoreContext.route!.isLoading ? <VSpinner /> : <VTable {...data} />}
                </VCardPanel>
              </Col>
            </Row>
          </Grid>
        );
      }}
    />
  );
};

export default injectIntl(DeliverySummaryRoute);
