import { BaseService } from './BaseServices';
import { AxiosPromise } from 'axios';
import { LanguageModel } from '../models/UserModel';

export class DashboardServices extends BaseService {
  constructor() {
    super('');
  }

  public getDeliveryGraph(params: any): AxiosPromise<any[]> {
    return this.httpAxios
      .get('getDeliveryGraph/', { params })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }

  public getAlerts(params: any): AxiosPromise<any[]> {
    return this.httpAxios
      .get('alerts/', { params })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }

  public getListLanguage(): AxiosPromise<any[]> {
    return this.httpAxios
      .get('listLanguages')
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }

  public changeLanguage(lang: LanguageModel): AxiosPromise<any> {
    return this.httpAxios
      .post('changeLanguage', lang.languageId)
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }

  public getHomeGraph(): AxiosPromise<any> {
    return this.httpAxios
      .get('getZohoGraphic')
      .then(response => {
        return response;
      })
      .catch(err => {
        throw err;
      });
  }
}
const dashboardServices = new DashboardServices();

export default dashboardServices;
