import React, { useContext, useState } from 'react';
import { IRouter } from 'rift-router';
import { VCardPanel, VSpinner } from 'gsi-vx-ui';

import IRecentAlert from '../../../core/types/RecentAlert';
import { ListAlert, ListAlertItem } from './styles';
import { AnchorButton, Button, Classes, Dialog, Intent, Tag, Tooltip } from '@blueprintjs/core';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { AppThemeContext } from '../../../App';

interface IRecentAlertProps extends InjectedIntlProps {
  alerts: IRecentAlert[];
  router?: IRouter;
  load: boolean;
}
const RecentAlert: React.FunctionComponent<IRecentAlertProps> = (props: IRecentAlertProps)=>{
    const ThemeContext = useContext(AppThemeContext);
    const [modalOpen, setModalOpen] = useState(false);
    const { alerts, load, intl } = props;

  const NavigateAlert = (alert: IRecentAlert) => {
    props.router!.to(`/delivery${getRouteParams(alert.route)}`);
  };

  const getRouteParams = (object: any) => {
    let params = '';
    console.log(object);
    const keys = Object.keys(object);
    if (keys.length > 0) {
      keys.map((key, index) => {
        const modifier = index === 0 ? '?' : '&&';
        params += `${modifier}${key}=${object[key]}`;
      });
    }
    return params;
  };

  const handleClose = () => {
    setModalOpen(false);
  };

  const goTo = (e: any, url: {}) => {
    // this.props.router!.search = {submodule:'module_3', child:'2', drop: 'pending_order'};
    props.router!.to('/delivery?submodule=module_3');
  };
  return (
    <React.Fragment>
      <VCardPanel
        headerText={`${intl.formatMessage({ id: 'home.dashboard.alerts' })}`}
        headerBackgroundColor={ThemeContext.headers}
        headerColor={'#FFFFFF'}
        headerIcon="warning-sign"
        bodyPadding="0px"
      >
        {load ? (
          <ListAlert>
            {alerts.map((alert: IRecentAlert, key: number) => (
              <ListAlertItem
                key={key}
                onClick={() => {
                  NavigateAlert(alert);
                }}
                icon={
                  <Tag round title={`${intl.formatMessage({ id: alert.label })}`} intent="danger">
                    {alert.total}
                  </Tag>
                }
                text={`${intl.formatMessage({ id: alert.label })}`}
              />
            ))}
          </ListAlert>
        ) : (
          <VSpinner />
        )}
      </VCardPanel>
      <Dialog
        icon="info-sign"
        onClose={handleClose}
        title={`${intl.formatMessage({ id: 'home.dashboard.alerts' })}`}
        isOpen={modalOpen}
      >
        <div className={Classes.DIALOG_BODY}>
          <p>Start the revolution. Unleash the power of data integration with Palantir Foundry.</p>
        </div>
        <div className={Classes.DIALOG_FOOTER}>
          <div className={Classes.DIALOG_FOOTER_ACTIONS}>
            <Tooltip content="This button is hooked up to close the dialog.">
              <Button onClick={handleClose}>Close</Button>
            </Tooltip>
            <AnchorButton intent={Intent.PRIMARY} href="https://www.palantir.com/palantir-foundry/" target="_blank">
              Visit the Foundry website
            </AnchorButton>
          </div>
        </div>
      </Dialog>
    </React.Fragment>
  );
};

export default injectIntl(RecentAlert);
