import React, { Component } from 'react';
import { Dialog } from '@blueprintjs/core';
import { VCardPanel, VSpinner } from 'gsi-vx-ui';
import { IImgInvoice } from '../../../stores/summary/DeliveriesStore/SummaryDeliveriesStore';
import { RenderEmptyData } from './NoData/RenderEmptyData';
import CustomComponent from '../../../../order_management/commons/CustomComponent';
import { AppThemeContext } from '../../../../../../../App';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { ITheme } from '../../../../../../../core/stores/AppStore';

interface DeliveriesViewInvoiceProps extends InjectedIntlProps {
  isOpen: boolean;
  invoiceImg: IImgInvoice;
  isLoading: boolean;
  onClose: () => void;
}

class DeliveriesViewInvoice extends Component<DeliveriesViewInvoiceProps> {
  render() {
    return (
      <AppThemeContext.Consumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <Dialog
              isOpen={this.props.isOpen}
              canEscapeKeyClose={true}
              canOutsideClickClose={true}
              onClose={this.props.onClose}
              style={{ width: '85%', padding: '0' }}
            >
              <VCardPanel
                headerText={this.props.intl.formatMessage({ id: 'delivery.summary.deliveries.viewerInvoice' })}
                bodyPadding={'10px'}
                headerBackgroundColor={themeContext.headers}
                headerColor={'#FFFFFF'}
                headerTextJustify={'center'}
                headerCustomComponent={<CustomComponent action={this.props.onClose} />}
              >
                {this.props.isLoading ? (
                  <VSpinner />
                ) : this.props.invoiceImg.data !== '' || this.props.invoiceImg.extension !== '' ? (
                  <img
                    style={{ width: '100%', maxWidth: '100%' }}
                    src={`data:image/${this.props.invoiceImg.extension};base64,${this.props.invoiceImg.data}`}
                  />
                ) : (
                  <RenderEmptyData />
                )}
              </VCardPanel>
            </Dialog>
          )
        }
      </AppThemeContext.Consumer>
    );
  }
}

export default injectIntl(DeliveriesViewInvoice);
