import React, { useContext } from 'react';
import HomeLayout from '../components/HomeLayout';
import { observer } from 'mobx-react-lite';
import homeStore, { HomeStore } from '../HomeStore';
import { StoreAppContext } from '../../../App';

export const HomeStoreContext = React.createContext<HomeStore>(homeStore);

const HomeContainer = observer(() => {
  const AppContext = useContext(StoreAppContext);
  AppContext.app.getOperations();
  AppContext.app.getAlerts();
  return (
    <HomeStoreContext.Provider value={homeStore}>
      <HomeLayout />
    </HomeStoreContext.Provider>
  );
});

export default HomeContainer;
