import BasePaginateService from '../../../../../../core/services/BasePaginateService';
import { AxiosPromise } from 'axios';

export class DeliverySummaryService extends BasePaginateService {
  constructor() {
    super('listDeliveryByShift');
  }
  public getDeliveryInvoice(url: string): AxiosPromise<any> {
    return this.httpAxios
      .get(url)
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
}

const deliverySummaryServices = new DeliverySummaryService();
export default deliverySummaryServices;
