import React from 'react';

interface IIframeSDProps{
  url?: string;
}
export const IframeSD = (props: IIframeSDProps)=>{

  const url = props.url?props.url:'http://192.168.137.1:8080/SmartConnect/flex/vxAppContainer.html';
    return (
      <div
        dangerouslySetInnerHTML={{
          __html:
            `<iframe src="${url}" frameBorder="0" width="100%" height="500px" allowFullScreen=true></iframe>`
        }}
      />
    );
};
