import { action, computed, observable } from 'mobx';

import authService from '../services/AuthServices';
import dashboardServices from '../services/DashboarServices';
import IRecentAlert from '../types/RecentAlert';
import { IOperationType } from '../types/OperationType';
import { AxiosResponse } from 'axios';
import IModulesNavigateType from '../types/ModulesNavigateType';
import { LanguageModel, UserModel } from '../models/UserModel';

export interface ITheme {
  primary: string;
  primary_70?: string;
  primary_50?: string;
  primary_20?: string;
  secondary: string;
  secondary_70?: string;
  secondary_50?: string;
  secondary_20?: string;
  accent_blue: string;
  accent_gray: string;
  accent_red: string;
  accent_green: string;
  dark_grey: string;
  sidebar: string;
  sidebar_text: string;
  headers: string;
}

export const themes = {
  light: {
    primary: '#FF6D00',
    primary_70: '#FF8F43',
    primary_50: '#FFC299',
    primary_20: '#FFE1CC',
    secondary: '#205CAA',
    secondary_70: '#396FAA',
    secondary_50: '#A5B5C4',
    secondary_20: '#D9DBDD',
    accent_blue: '#4bacef',
    accent_gray: '#efefef',
    accent_red: '#f24444',
    accent_green: '#20a8a4',
    dark_grey: '#424242',
    sidebar: '#424242',
    sidebar_text: '#FFFFFF',
    headers: '#396FAA'
  },
  alternative: {
    primary: '#205CAA',
    primary_70: '#396FAA',
    primary_50: '#A5B5C4',
    primary_20: '#D9DBDD',
    secondary: '#FF6D00',
    secondary_70: '#FF8F43',
    secondary_50: '#FFC299',
    secondary_20: '#FFE1CC',
    accent_blue: '#4BACEF',
    accent_gray: '#EFEFEF',
    accent_red: '#F24444',
    accent_green: '#20A8A4',
    dark_grey: '#424242',
    sidebar: '#205CAA',
    sidebar_text: '#FFFFFF',
    headers: '#373d44'
  }
};

export class AppStore {
  @observable
  isLoading: boolean;

  @observable
  isLoadingLanguage: boolean;

  @observable
  public isOpenProcess: boolean;

  @observable
  public isCollapseMenu: boolean;

  @observable
  recentAlerts: IRecentAlert[];

  @observable
  listLanguage: LanguageModel[];

  @observable
  operations: IOperationType[];

  @observable
  commodityType: string;

  @observable
  commodityTypeOptions: any[];

  @observable
  modulesNavigate: IModulesNavigateType;

  @observable
  lang: LanguageModel;

  @observable
  theme: ITheme;

  @observable
  user: UserModel;

  @observable
  branch: string;

  constructor() {
    this.isLoading = false;
    this.isLoadingLanguage = false;
    this.commodityType = 'propane';
    this.commodityTypeOptions = [
      {
        key: 'propane',
        value: 'propane',
        text: 'Propane',
        selected: true,
        image: { avatar: true, src: '/images/avatar/small/jenny.jpg' }
      },
      {
        key: 'crude',
        value: 'crude',
        text: 'Crude',
        selected: false,
        image: { avatar: true, src: '/images/avatar/small/jenny.jpg' }
      },
      {
        key: 'chemical',
        value: 'chemical',
        text: 'Chemical',
        selected: false,
        image: { avatar: true, src: '/images/avatar/small/jenny.jpg' }
      }
    ];
    this.isCollapseMenu = false;
    this.isOpenProcess = false;
    this.recentAlerts = [];
    this.operations = [];
    this.modulesNavigate = {};
    this.lang = { languageCode: 'en', languageId: 1, primary: false };
    this.theme = themes['light'];
    this.user = new UserModel();
    this.listLanguage = [];
    this.branch = '';
  }

  getOperations() {
    this.setLoading(true);
    dashboardServices
      .getAll('operation', {})
      .then((response: AxiosResponse<IOperationType[]>) => {
        this.setOperations(response.data);
      })
      .catch((error: any) => {
        console.log(error);
      })
      .finally(() => {
        this.setLoading(false);
      });
  }

  getAlerts() {
    this.setLoading(true);
    dashboardServices
      .getAlerts({})
      .then((response: AxiosResponse<IRecentAlert[]>) => {
        this.getAlertsMessages(response.data);
      })
      .catch((error: any) => {
        console.log(error);
      })
      .finally(() => {
        this.setLoading(false);
      });
  }

  @action
  getAlertsMessages(alerts: IRecentAlert[]) {
    this.setRecentAlerts(
      alerts.map((value: IRecentAlert) => {
        switch (value.alertType) {
          case 'INCOMPLETE_ORDERS':
            value.label = 'home.alerts.incomplete_orders';
            value.route = { submodule: 'module_2', child: '3' };
            break;
          case 'UNGEOCODE_ACCOUNTS':
            value.label = 'home.alerts.ungeocode_accounts';
            value.route = {};
            break;
          case 'DUPLICATE_ORDERS':
            value.label = 'home.alerts.duplicate_orders';
            value.route = { submodule: 'module_2', child: '2', drop: 'duplicate' };
            break;
          case 'NO_DELIVERY_FOR_REVIEW':
            value.label = 'home.alerts.no_delivery_for_review';
            value.route = { submodule: 'module_2', child: '2', drop: 'no_deliveries' };
            break;
          case 'UNDELIVERY_STOP_FOR_REVIEW':
            value.label = 'home.alerts.undelivery_stop_for_review';
            value.route = { submodule: 'module_2', child: '2', drop: 'pending_order' };
            break;
          default:
            value.label = 'home.alerts.incomplete_orders';
            value.route = { submodule: 'module_2', child: '3' };
        }
        return value;
      })
    );
  }

  @action
  setOperations(operations: IOperationType[]) {
    this.operations = operations;
  }

  @action
  setLoading(value: boolean) {
    this.isLoading = value;
  }

  @action
  setLoadingLanguage(value: boolean) {
    this.isLoadingLanguage = value;
  }

  @action
  public setIsOpenProcess(value: boolean) {
    this.isOpenProcess = value;
  }

  @action
  public setIsCollapseMenu(value: boolean) {
    this.isCollapseMenu = value;
  }

  @action
  public setStatusCollapseMenu() {
    this.isCollapseMenu = !this.isCollapseMenu;
  }

  @action
  public setModulesNavigate(moduleNavigate: IModulesNavigateType) {
    this.modulesNavigate = moduleNavigate;
  }

  @computed
  public get isLogged() {
    return !!authService.isLoggedIn();
  }

  @action
  public resetUser() {
    authService.clearToken();
  }

  public changeLang(value: LanguageModel) {
    this.setLang(value);
    dashboardServices.changeLanguage(value);
  }

  public getListLanguage() {
    this.setLoadingLanguage(true);
    dashboardServices
      .getListLanguage()
      .then((response: AxiosResponse<any>) => {
        if (response.data && response.data.content) {
          this.setListLanguage(response.data.content);
        }
        this.setLoadingLanguage(false);
      })
      .catch((error: any) => {
        console.log(error);
        this.setLoadingLanguage(false);
      });
  }

  @action
  setLang(value: LanguageModel) {
    this.lang = value;
  }

  @action
  setListLanguage(value: LanguageModel[]) {
    this.listLanguage = value;
  }

  @action
  setRecentAlerts(alert: IRecentAlert[]) {
    this.recentAlerts = alert;
  }

  @action
  setTheme(value: ITheme) {
    this.theme = value;
  }

  @action
  setUser(user: UserModel) {
    this.user = user;
  }

  @action
  setBranch(value: string) {
    this.branch = value;
  }

  public storeUserCredentials(credentials: any) {
    authService.storeUserCredentials(credentials);
  }

  public logout() {
    authService.clearToken();
  }
}

const appStore = new AppStore();

export default appStore;
