import React, { useContext, useEffect } from 'react';

import { Observer } from 'mobx-react-lite';
import { Alignment, Button, Icon, Menu, MenuDivider, MenuItem, Navbar, Popover, Position } from '@blueprintjs/core';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import logo from '../../../assets/images/common/logo.png';
import { MenuLogo, ThemeCircle } from './styles';

import { themes } from '../../stores/AppStore';
import { ThemeTypes } from '../../types/ThemeTypes';
import { VSelectField, VSpinner } from 'gsi-vx-ui';
import { StoreAppContext } from '../../../App';
import { useRouter } from 'rift-router';
import { LanguageModel } from '../../models/UserModel';

const MainNavbar = (props: InjectedIntlProps) => {
  const AppStoreContext = useContext(StoreAppContext);

  useEffect(() => {
    AppStoreContext.app.getListLanguage();
  }, []);

  const router = useRouter();

  const collapseMenu = (e: any) => {
    AppStoreContext.app!.setStatusCollapseMenu();
  };

  const showProcess = (e: any) => {
    AppStoreContext.app!.setIsOpenProcess(true);
  };

  const linkTo = (path: string) => {
    router!.to(path);
  };

  const changeTheme = (theme: ThemeTypes) => {
    if (AppStoreContext.app) {
      AppStoreContext.app.setTheme(themes[theme]);
    }
  };

  const changeLang = (lang: LanguageModel) => {
    AppStoreContext.app.changeLang(lang);
  };

  const logout = () => {
    AppStoreContext.app!.logout();
    linkTo('/login');
  };

  return (
    <Navbar>
      <Navbar.Group align={Alignment.LEFT}>
        <Navbar.Heading onClick={(e: any) => collapseMenu(e)}>
          <Icon icon="menu" />
        </Navbar.Heading>
        <Navbar.Heading>
          <MenuLogo onClick={(e: any) => linkTo('/')}>
            <img src={logo} style={{ height: 40 }} />
          </MenuLogo>
        </Navbar.Heading>
        <Navbar.Divider />
      </Navbar.Group>
      <Navbar.Group align={Alignment.RIGHT}>
        <VSelectField
          label={''}
          icon={'form'}
          id={'branch'}
          inline
          defaultText={'Branch'}
          margin={'0'}
          minimal
          value={AppStoreContext.app!.branch}
          fill
          onChange={(value: string) => AppStoreContext.app!.setBranch(value)}
          options={[{ value: 1, label: 'SHELBYVILLE, MI' }, { value: 2, label: 'Chicago, Illinois' }]}
        />
        <Button
          icon={<Icon icon="notifications-updated" iconSize={20} />}
          minimal
          large
          onClick={(e: any) => showProcess(e)}
        />

        <Popover
          content={
            <Menu>
              <MenuItem icon="cog" text={`${props.intl.formatMessage({ id: 'layout.navbar.config' })}`} />
              <MenuItem icon="people" text={`${props.intl.formatMessage({ id: 'layout.navbar.account' })}`} />
              <MenuItem
                icon="contrast"
                text="Themes"
                popoverProps={{
                  hoverCloseDelay: 400,
                  captureDismiss: false
                }}
              >
                <Observer>
                  {() => (
                    <>
                      <MenuItem
                        icon={<ThemeCircle themeProps={themes.light} />}
                        text="Standard"
                        active={AppStoreContext.app!.theme === themes['light']}
                        onClick={(e: any) => changeTheme('light')}
                      />
                      <MenuItem
                        icon={<ThemeCircle themeProps={themes.alternative} />}
                        text="Alternative"
                        active={AppStoreContext.app!.theme === themes['alternative']}
                        onClick={(e: any) => changeTheme('alternative')}
                      />
                    </>
                  )}
                </Observer>
              </MenuItem>
              <Observer
                render={() => (
                  <MenuItem icon="translate" text={props.intl.formatMessage({ id: 'settings.lang' })}>
                    {AppStoreContext.app.isLoadingLanguage ? (
                      <VSpinner size={25} />
                    ) : (
                      AppStoreContext.app.listLanguage.map(item => (
                        <MenuItem
                          text={props.intl.formatMessage({ id: `app.${item.languageCode}` })}
                          active={AppStoreContext.app.lang.languageCode === item.languageCode}
                          onClick={(e: any) => changeLang(item)}
                        />
                      ))
                    )}
                  </MenuItem>
                )}
              />

              <MenuDivider />
              <MenuItem
                icon="log-out"
                text={`${props.intl.formatMessage({ id: 'layout.navbar.logout' })}`}
                onClick={logout}
              />
            </Menu>
          }
          position={Position.BOTTOM}
        >
          <Button icon={<Icon icon="user" iconSize={20} />} minimal large />
        </Popover>
      </Navbar.Group>
    </Navbar>
  );
};

export default injectIntl(MainNavbar);
