import React, { Component } from 'react';
import { Col, Grid, Row } from 'react-flexbox-grid';

import TaskCard from './TaskCard';
import { IOperationType } from '../../../../core/types/OperationType';
import { InjectedIntlProps, injectIntl } from 'react-intl';

interface IProp extends InjectedIntlProps {
  load: boolean;
  operations: IOperationType[];
}

class HomeTaskCard extends Component<IProp> {
  constructor(props: IProp) {
    super(props);
  }

  render() {
    const { intl, operations } = this.props;
    return (
      <Grid fluid style={{ paddingRight: 0, paddingLeft: 0, margin: '20px 0px' }}>
        {operations.map((value: IOperationType) => {
          switch (value.dashboardOperation) {
            case 'ACCOUNTS':
              return (
                <Row center={'xs'}>
                  <Col xs={12} sm={12} md={6}>
                    <TaskCard
                      background="#f2711c"
                      headerText={`${intl.formatMessage({ id: 'home.dashboard.import_accounts' })}`}
                      pendingUpdateAmount={value.total - value.updates}
                      totalAccount={value.total}
                      updateText={`${intl.formatMessage({ id: 'home.dashboard.last_update' })}`}
                      icon="people"
                      load={this.props.load}
                    />
                  </Col>
                </Row>
              );
            case 'ORDERS':
              return (
                <Row center={'xs'} style={{marginTop: '10px'}}>
                  <Col xs={12} sm={12} md={6}>
                    <TaskCard
                      background="#2185D0"
                      headerText={`${intl.formatMessage({ id: 'home.dashboard.import_orders' })}`}
                      pendingUpdateAmount={value.total - value.updates}
                      totalAccount={value.total}
                      updateText={`${intl.formatMessage({ id: 'home.dashboard.last_update' })}`}
                      icon="properties"
                      load={this.props.load}
                    />
                  </Col>
                </Row>
              );
            case 'PACKAGE_PRICING':
              return (
                <Row center={'xs'} style={{marginTop: '10px'}}>
                  <Col xs={12} sm={12} md={6}>
                    <TaskCard
                      background="#00B5AD"
                      headerText={`${intl.formatMessage({ id: 'home.dashboard.package_pricing' })}`}
                      pendingUpdateAmount={value.total - value.updates}
                      totalAccount={value.total}
                      updateText={`${intl.formatMessage({ id: 'home.dashboard.last_update' })}`}
                      icon="box"
                      load={this.props.load}
                    />
                  </Col>
                </Row>
              );
          }
        })}
      </Grid>
    );
  }
}

export default injectIntl(HomeTaskCard);
