import React, { useContext } from 'react';
import { observer } from 'mobx-react-lite';
import { ITabsPanelTypes, VTabsPanel } from 'gsi-vx-ui';
import { IconName } from '@blueprintjs/core';

import { StoreAppContext } from '../../../App';
import TrackingContainer from '../modules/tracking/containers/tracking/TrackingContainer';
import OrderManagementContainer from '../modules/order_management/containers/OrderManagementContainer';
import MessagingContainer from '../modules/messaging/containers/messaging/MessagingContainer';
import { SummaryContainer } from '../modules/sumary';
import { DeliveryDispatch } from '../modules/dispatch';
import { StoreDeliveryContext } from '../container/DeliveryContainer';
import { useRouter } from 'rift-router';

const DeliveryLayout = observer(() => {
  const AppStoreContext = useContext(StoreAppContext);
  const DeliveryStoreContext = useContext(StoreDeliveryContext);
  const router = useRouter();

  if (router.search.hasOwnProperty('submodule')) {
    DeliveryStoreContext!.setActiveModule(router.search.submodule);
  } else {
    DeliveryStoreContext!.setActiveModule('module_1');
  }

  const handleChange = (data: ITabsPanelTypes) => {
    DeliveryStoreContext!.setActiveModule(data.key);
  };
  const changeColor = (module: number, color: IconName) => {
    DeliveryStoreContext!.setColor(module, color);
  };

  const modules: ITabsPanelTypes[] = [
    {
      icon: { intent: 'primary', icon: 'folder-close', iconSize: 16 },
      label: 'Delivery Summary',
      key: 'module_1',
      content: <SummaryContainer />
    },
    {
      icon: { intent: 'primary', icon: 'folder-close', iconSize: 16 },
      label: 'Order Management',
      key: 'module_2',
      content: <OrderManagementContainer changeColor={changeColor} />
    },
    {
      icon: { intent: 'primary', icon: 'folder-close', iconSize: 16 },
      label: 'Dispatch',
      key: 'module_3',
      content: <DeliveryDispatch />
    },
    {
      icon: { intent: 'primary', icon: 'folder-close', iconSize: 16 },
      label: 'Tracking',
      key: 'module_4',
      content: <TrackingContainer />
    },
    {
      icon: { intent: 'primary', icon: 'folder-close', iconSize: 16 },
      label: 'Messaging',
      key: 'module_5',
      content: <MessagingContainer />
    }
  ];
  return (
    <VTabsPanel
      active={DeliveryStoreContext.activeModule}
      padding={'10px'}
      tabList={modules}
      handleChange={handleChange}
    />
  );
});

export default DeliveryLayout;
