import React, { useContext } from 'react';
import { AppThemeContext } from '../../../../App';
import { VPagination } from './index';
import { InjectedIntlProps, injectIntl } from 'react-intl';

interface PageProps extends InjectedIntlProps {
  totalRecords: number;
  currentPage: number;
  pageLimit: number;
  onPageChanged(data: any): void;
}
const PagePaginatorComponent: React.FunctionComponent<PageProps> = (props: PageProps): JSX.Element => {
  const ThemeContext = useContext(AppThemeContext);
  return (
    <VPagination
      totalRecords={props.totalRecords}
      currentPage={props.currentPage}
      pageLimit={props.pageLimit}
      pageNeighbours={3}
      itemsByPage={[
        { label: '25', value: 25 },
        { label: '50', value: 50 },
        { label: '100', value: 100 },
        { label: '200', value: 200 }
      ]}
      onPageChanged={props.onPageChanged}
      style={{
        backgroundColor: ThemeContext.headers,
        color: 'white',
        display: 'flex',
        width: '100%',
        border: '0px',
        justifyContent: 'flex-end',
        borderRadius: '0px'
      }}
      labels={{
        itemsByPage: props.intl.formatMessage({ id: 'commons.elemPage' })
      }}
      customerStyle={{
        pageSelectedBackgroundColor: ThemeContext.accent_blue,
        pageSelectedColor: ThemeContext.sidebar_text,
        iconColor: ThemeContext.sidebar_text
      }}
    />
  );
};

export default injectIntl(PagePaginatorComponent);
